import React, { Component } from 'react';
import { Accordion } from 'react-bootstrap';
import { PopUpOption, ColumnOption, HideOlderOption } from './Utils'
import ThemeSettings from './Elements/ThemeSettings';
const Background = React.lazy(() => import('./Elements/Background'));
const CustomCss = React.lazy(() => import('./Elements/CustomCss'));
const OnSiteUploads = React.lazy(() => import('./Elements/OnSiteUpload'));
const DesignCardStyle = React.lazy(() => import('./Elements/CardStyle'));
const Advanced = React.lazy(() => import('./Elements/Advanced'));
const CTAButton = React.lazy(() => import('./Elements/CTAButton'));
class HorizontalColumnTemplate extends Component {
    state = {
        background: { status: false, color: "#fff", image: null, transparent: 0, backgroundImageId: 0 },
        cardStyle: { cardColor: "#000000", borderRadius: 10, fontFamily: null, fontVarient: null, fontSize: 15, textColor: "#fff", authorColor: "#fff", socialIconColor: "#fff", },
        advanced: { time: false, author: false, hideContent: false, shareOptions: false, socialActions: false },
        onSiteUpload: { bgColor: "#fff", borderRadius: 0, fontFamily: null, fontVarient: null, fontSize: 10, textColor: "#fff", buttonText: "Scan QR or Tap here to upload UGC", buttonStatus: false, qrCodeStatus: false },
        themeSettings: { suffleAssets: false, showmore: false, autoLoad: false, trimContent: false, bestFit: false, popupSlideShow: true, hideOldAssets: { status: false, range: null }, postPopUp: null, popUpUrl: null, numberOfPosts: 20, columns: { label: "Responsive", value: 0 }, postPadding: 15, hideTextTypeAssets: false, minPostWitdh: 300, playFullVideo: false, hideUrls: false, timePerSlide: 0, scrollSpeed: 0, popUpduration: 15, duplicatePostId: false, mobilePopup: false },
        customCss: { cssStatus: false, css: "" },
        cta: { ctaStatus: 0, ctaTextColor: '', ctaButtonColor: '', buttonText: '', buttonUrl: '' },
        urlError: false

    }
    componentDidMount() {
        const { props } = this
        const { themeDefaultOption } = props.selectedThemeData
        const { onSiteSetting, fonts } = props
        const cardFontStyle = themeDefaultOption.css_font ? fonts.filter((itemf) => itemf.family == themeDefaultOption.css_font)[0] : fonts[0]
        var font_varient = themeDefaultOption.font_varient && cardFontStyle ? { label: cardFontStyle.variants.filter((varient) => varient == themeDefaultOption.font_varient)[0], value: cardFontStyle.variants.filter((varient) => varient == themeDefaultOption.font_varient)[0] } : {}

        const onSiteFontStyle = onSiteSetting && onSiteSetting.onsite_css_font_btn ? fonts.filter((itemf) => itemf.family == onSiteSetting.onsite_css_font_btn)[0] : fonts[0]
        var onSitefont_varient = onSiteSetting && onSiteSetting.onsite_font_varient_btn && onSiteFontStyle && onSiteFontStyle.variants ? { label: onSiteFontStyle.variants.filter((varient) => varient == onSiteSetting.onsite_font_varient_btn)[0], value: onSiteFontStyle.variants.filter((varient) => varient == onSiteSetting.onsite_font_varient_btn)[0] } : {}
        this.setState({
            background: { status: themeDefaultOption.transparent == 1 ? false : true, color: themeDefaultOption.backgroundColor ? themeDefaultOption.backgroundColor : "#fff", image: themeDefaultOption.backgroundImage ? themeDefaultOption.backgroundImage : null, transparent: themeDefaultOption.transparent == 1 ? 1 : 0, backgroundImageId: themeDefaultOption.backgroundImageId ? themeDefaultOption.backgroundImageId : 0 },
            cardStyle: { cardColor: themeDefaultOption.cardColor ? themeDefaultOption.cardColor : "#000000", borderRadius: 10, fontFamily: cardFontStyle, fontVarient: font_varient, fontSize: themeDefaultOption.fontSize ? themeDefaultOption.fontSize : 15, textColor: themeDefaultOption.fontColor ? themeDefaultOption.fontColor : "#fff", authorColor: themeDefaultOption.authorColor ? themeDefaultOption.authorColor : "#fff", socialIconColor: themeDefaultOption.iconColor ? themeDefaultOption.iconColor : "#fff", iconType: themeDefaultOption.iconType == 1 ? true : false },
            advanced: { time: themeDefaultOption.postTime ? true : false, author: themeDefaultOption.postAuthor ? true : false, hideContent: themeDefaultOption.hideContent == 1 ? true : false, shareOptions: themeDefaultOption.shareOption == 1 ? true : false, socialActions: themeDefaultOption.socialAction == 1 ? true : false },

            onSiteUpload: { bgColor: onSiteSetting.onsite_btn_color ? onSiteSetting.onsite_btn_color : "#fff", borderRadius: onSiteSetting.btn_radius ? onSiteSetting.btn_radius : 0, fontFamily: onSiteFontStyle, fontVarient: onSitefont_varient, fontSize: onSiteSetting.btn_fontSize ? onSiteSetting.btn_fontSize : 10, textColor: onSiteSetting.onsite_btn_txt_color ? onSiteSetting.onsite_btn_txt_color : "#fff", buttonText: onSiteSetting.onsite_qr_code_btn_text ? onSiteSetting.onsite_qr_code_btn_text : "Scan QR or Tap here to upload UGC", buttonStatus: onSiteSetting.onsite_button_status ? true : false, qrCodeStatus: onSiteSetting.onsite_qr_code_status == 1 ? true : false },

            themeSettings: { suffleAssets: themeDefaultOption.randomizePost == 1 ? true : false, showmore: themeDefaultOption.loadMoreStatus ? true : false, autoLoad: themeDefaultOption.autoScrollStatus == 1 ? true : false, trimContent: themeDefaultOption.trimcontent == undefined ? true : themeDefaultOption.trimcontent == 1 ? true : false, bestFit: themeDefaultOption.fitRow ? themeDefaultOption.fitRow == 1 ? true : false : true, popupSlideShow: themeDefaultOption.popupSlideShow ? true : false, hideOldAssets: { status: themeDefaultOption.showHideOlderPost ? true : false, range: themeDefaultOption.showHideOlderPost ? HideOlderOption.filter(option => option.value == themeDefaultOption.showHideOlderPost)[0] : null, showHideOldPost: themeDefaultOption.showHideOldPost ? new Date((themeDefaultOption.showHideOldPost) * 1000) : new Date() }, postPopUp: themeDefaultOption.postFeatured && String(themeDefaultOption.postFeatured).length > 0 ? PopUpOption.filter((itemf) => itemf.value == themeDefaultOption.postFeatured)[0] : PopUpOption[1], popUpUrl: themeDefaultOption.customUrl ? themeDefaultOption.customUrl : null, numberOfPosts: themeDefaultOption.numberOfPosts ? themeDefaultOption.numberOfPosts : 20, columns: themeDefaultOption.numberOfCoumn ? ColumnOption.filter((itemf) => itemf.value == themeDefaultOption.numberOfCoumn)[0] : { label: "Responsive", value: 0 }, postPadding: themeDefaultOption.padding ? themeDefaultOption.padding : 3, hideTextTypeAssets: false, minPostWitdh: themeDefaultOption.minimumPostWidth ? themeDefaultOption.minimumPostWidth : 300, playFullVideo: false, hideUrls: themeDefaultOption.hideUrls == 1 ? true : false, timePerSlide: themeDefaultOption.slideDelay ? themeDefaultOption.slideDelay : 15, scrollSpeed: 0, popUpduration: themeDefaultOption.slideDuration, duplicatePostId: themeDefaultOption.duplicatePostId == 1 ? true : false, mobilePopup: themeDefaultOption.mobilePopup ? true : false},

            customCss: { cssStatus: themeDefaultOption.cssStatus == 1 ? true : false, css: themeDefaultOption.css ? themeDefaultOption.css : "" },

            cta: { ctaStatus: themeDefaultOption.ctaStatus == 1 ? true : false, ctaTextColor: themeDefaultOption.ctaTextColor ? themeDefaultOption.ctaTextColor : '#fff', ctaButtonColor: themeDefaultOption.ctaButtonColor ? themeDefaultOption.ctaButtonColor : '#000', buttonText: themeDefaultOption.buttonText ? themeDefaultOption.buttonText : 'Apply Now', buttonUrl: themeDefaultOption.buttonUrl ? themeDefaultOption.buttonUrl : '' }

        }, () => this.props.tempFormData(this.state))

    }

    onClickEnableBackground = event => {
        const { background } = this.state;
        background.status = event.target.checked
        if (!event.target.checked) background.transparent = 1
        else background.transparent = 0
        this.setState({ background })
    }

    onClickBackgroundColor = (color) => {
        const { background } = this.state;
        background.color = color
        this.setState({ background })
    }

    onClickTouploadImage = (value, backgroundImageId, callBack) => {
        const { background } = this.state;
        background.image = value
        background.backgroundImageId = backgroundImageId
        this.setState({ background }, () => callBack())
    }

    backgroundImageRemove = event => {
        const { background } = this.state;
        background.image = null
        background.backgroundImageId = 0
        this.setState({ background })
    }

    onClickEnableTime = event => {
        const { advanced } = this.state;
        advanced.time = event.target.checked
        this.setState({ advanced })
    }

    onClickEnableAuthor = event => {
        const { advanced } = this.state;
        advanced.author = event.target.checked
        this.setState({ advanced })
    }

    onClickEnableHideContent = event => {
        const { advanced } = this.state;
        advanced.hideContent = event.target.checked
        this.setState({ advanced })
    }

    onClickEnableCustomCss = event => {
        const { customCss } = this.state;
        customCss.cssStatus = event.target.checked
        this.setState({ customCss })
    }

    onChangeCss = event => {
        const { customCss } = this.state;
        customCss.css = event
        this.setState({ customCss })
    }

    onClickDuplicatePostId = event => {
        const { themeSettings } = this.state;
        themeSettings.duplicatePostId = event.target.checked
        this.setState({ themeSettings })
    }

    onClickEnableSuffleAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.suffleAssets = event.target.checked
        this.setState({ themeSettings })
    }

    onClickShowMore = event => {
        const { themeSettings } = this.state;
        themeSettings.showmore = event.target.checked
        themeSettings.autoLoad = event.target.checked ? false : true
        this.setState({ themeSettings })
    }

    onClickAutoLoad = event => {
        const { themeSettings } = this.state;
        themeSettings.autoLoad = event.target.checked
        themeSettings.showmore = event.target.checked ? false : true
        this.setState({ themeSettings })
    }

    onClickTrimContent = event => {
        const { themeSettings } = this.state;
        themeSettings.trimContent = event.target.checked
        this.setState({ themeSettings })
    }

    onClickPopUpSlideShow = event => {
        const { themeSettings } = this.state;
        themeSettings.popupSlideShow = event.target.checked
        this.setState({ themeSettings })
    }

    onClickMobilePopUp = event => {
        const { themeSettings } = this.state;
        themeSettings.mobilePopup = event.target.checked
        this.setState({ themeSettings })
    }

    onClickHideOldAssets = event => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.status = event.target.checked
        this.setState({ themeSettings })
    }

    onSelectHideOlderPost = (event) => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.range = event
        this.setState({ themeSettings })
    }

    onSelectHideDateRange = (event) => {
        const { themeSettings } = this.state;
        themeSettings.hideOldAssets.showHideOldPost = event
        this.setState({ themeSettings })
    }

    onNumberOfPosts = event => {
        const { themeSettings } = this.state;
        if (event.target.value <= 100) {
            themeSettings.numberOfPosts = event.target.value
            this.setState({ themeSettings })
        }
    }
    
    onPostPadding = event => {
        const { themeSettings } = this.state;
        themeSettings.postPadding = event.target.value > 30 ? 30 : event.target.value
        this.setState({ themeSettings })
    }

    onSelectPostPopUp = event => {
        const { themeSettings } = this.state;
        themeSettings.postPopUp = event
        this.setState({ themeSettings })
    }

    onPopUpUrl = event => {
        const { themeSettings } = this.state;
        themeSettings.popUpUrl = event.target.value
        this.setState({ themeSettings })
    }

    onSelectColumns = event => {
        const { themeSettings } = this.state;
        themeSettings.columns = event
        this.setState({ themeSettings })
    }

    onChangeCardColor = (cardColor) => {
        const { cardStyle } = this.state;
        cardStyle.cardColor = cardColor
        this.setState({ cardStyle })
    }

    onCardBorderRadius = event => {
        const { cardStyle } = this.state;
        cardStyle.borderRadius = event.target.value > 30 ? 30 : event.target.value
        this.setState({ cardStyle })
    }

    onCardFontFamily = (event) => {
        const { cardStyle } = this.state;
        cardStyle.fontFamily = event
        this.setState({ cardStyle })
    }

    onCardFontVarient = (event) => {
        const { cardStyle } = this.state;
        cardStyle.fontVarient = event
        this.setState({ cardStyle })
    }

    onCardFontSize = event => {
        const { cardStyle } = this.state;
        cardStyle.fontSize = event.target.value > 30 ? 30 : event.target.value
        this.setState({ cardStyle })
    }

    onCardTextColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.textColor = value
        this.setState({ cardStyle })
    }

    onCardAuthorColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.authorColor = value
        this.setState({ cardStyle })
    }

    onCardSocialColor = (value) => {
        const { cardStyle } = this.state;
        cardStyle.socialIconColor = value
        this.setState({ cardStyle })
    }

    onClickIconType = event => {
        const { cardStyle } = this.state;
        cardStyle.iconType = event.target.checked
        this.setState({ cardStyle })
    }

    onOnSiteBgColor = (value) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.bgColor = value
        this.setState({ onSiteUpload })
    }

    onOnSiteBorderRadius = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.borderRadius = event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteFontFamily = (event) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontFamily = event
        this.setState({ onSiteUpload })
    }

    onOnSiteFontVarient = (event) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontVarient = event
        this.setState({ onSiteUpload })
    }

    onOnSiteFontSize = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.fontSize = event.target.value > 30 ? 30 : event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteTextColor = (value) => {
        const { onSiteUpload } = this.state;
        onSiteUpload.textColor = value
        this.setState({ onSiteUpload })
    }

    onOnSiteButtonText = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.buttonText = event.target.value
        this.setState({ onSiteUpload })
    }

    onOnSiteButtonStatus = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.buttonStatus = event.target.checked
        this.setState({ onSiteUpload })
    }

    onOnSiteQrCodeStatus = event => {
        const { onSiteUpload } = this.state;
        onSiteUpload.qrCodeStatus = event.target.checked
        this.setState({ onSiteUpload })
    }

    onCtaButtonStatus = event => {
        const { cta } = this.state
        cta.ctaStatus = event.target.checked
        this.setState({ cta })
    }

    onCtaButtonText = (text) => {
        const { cta } = this.state
        cta.buttonText = text ? text.label : ''
        this.setState({ cta })
    }

    onCtaTextColor = (value) => {
        const { cta } = this.state
        cta.ctaTextColor = value
        this.setState({ cta })
    }

    onCtaButtonColor = (value) => {
        const { cta } = this.state
        cta.ctaButtonColor = value
        this.setState({ cta })
    }

    onCtaButtonUrl = (event) => {
        const { cta } = this.state
        if (event.target.value.startsWith('https://') || event.target.value.startsWith('http://')) {
            cta.buttonUrl = event.target.value
            this.setState({ cta: cta, urlError: false })
        }
        else {
            cta.buttonUrl = event.target.value
            this.setState({ cta, urlError: true })
        }
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState != this.state) this.props.tempFormData(this.state)
    }

    render() {
        const { fonts } = this.props
        const { background, cardStyle, advanced, onSiteUpload, themeSettings, customCss, cta, urlError } = this.state;

        return <Accordion defaultActiveKey="1">
            <Background onClickEnableBackground={this.onClickEnableBackground} onClickBackgroundColor={this.onClickBackgroundColor} background={background} onClickTouploadImage={this.onClickTouploadImage} backgroundImageRemove={this.backgroundImageRemove} />

            <DesignCardStyle cardStyle={cardStyle} onChangeCardColor={this.onChangeCardColor} onCardBorderRadius={this.onCardBorderRadius} onCardFontSize={this.onCardFontSize} fonts={fonts} onCardFontFamily={this.onCardFontFamily} onCardFontVarient={this.onCardFontVarient} onCardTextColor={this.onCardTextColor} onCardAuthorColor={this.onCardAuthorColor} onCardSocialColor={this.onCardSocialColor} onClickIconType={this.onClickIconType} />

            <Advanced advanced={advanced} onClickEnableTime={this.onClickEnableTime} onClickEnableAuthor={this.onClickEnableAuthor} onClickEnableHideContent={this.onClickEnableHideContent} selectedTheme={this.props.selectedTheme}/>

            <CTAButton cta={cta} urlError={urlError} onCtaButtonColor={this.onCtaButtonColor} onCtaButtonStatus={this.onCtaButtonStatus} onCtaButtonText={this.onCtaButtonText} onCtaButtonUrl={this.onCtaButtonUrl} onCtaTextColor={this.onCtaTextColor} />

            <OnSiteUploads onSiteUpload={onSiteUpload} onOnSiteBgColor={this.onOnSiteBgColor} onOnSiteBorderRadius={this.onOnSiteBorderRadius} onOnSiteFontSize={this.onOnSiteFontSize} onOnSiteTextColor={this.onOnSiteTextColor} onOnSiteButtonText={this.onOnSiteButtonText} onOnSiteButtonStatus={this.onOnSiteButtonStatus} onOnSiteQrCodeStatus={this.onOnSiteQrCodeStatus} onOnSiteFontFamily={this.onOnSiteFontFamily} onOnSiteFontVarient={this.onOnSiteFontVarient} fonts={fonts} />

            <ThemeSettings themeSettings={themeSettings} onClickDuplicatePostId={this.onClickDuplicatePostId} onClickEnableSuffleAssets={this.onClickEnableSuffleAssets} onClickShowMore={this.onClickShowMore} onClickAutoLoad={this.onClickAutoLoad} onClickTrimContent={this.onClickTrimContent} onClickHideOldAssets={this.onClickHideOldAssets} onSelectHideOlderPost={this.onSelectHideOlderPost} onSelectHideDateRange={this.onSelectHideDateRange} onClickPopUpSlideShow={this.onClickPopUpSlideShow} onClickMobilePopUp={this.onClickMobilePopUp} onSelectPostPopUp={this.onSelectPostPopUp} onPopUpUrl={this.onPopUpUrl} onNumberOfPosts={this.onNumberOfPosts} onPostPadding={this.onPostPadding} selectedTheme={this.props.selectedTheme} onSelectColumns={this.onSelectColumns} />

            <CustomCss customCss={customCss} onClickEnableCustomCss={this.onClickEnableCustomCss} onChangeCss={this.onChangeCss} />
        </Accordion>
    }
}

export default HorizontalColumnTemplate;